<template>
    <div>
        <div class="cart">
            <div  class="left" style="color: white"  @click="listIsShow">
                <div class="icon">
                    <div class="logo" :class="{active:totalCount > 0}">
                        <i class="layout-shopping_cart"></i>
                    </div>
                    <span class="qipao" v-show="totalCount > 0">{{totalCount}}</span>
                </div>
                <div class="totalPrice" :class="{active:totalPrice>0}">
                    <span>{{totalPrice}}</span>
                </div>
                <div class="deliveryPrice">
                    <span>{{seller.deliveryPrice}}</span>
                </div>
            </div>
            <div class="right active">
                <span>{{payText}}</span>
            </div>
            <div class="balls">
                <div class="ballWarp" v-for="(ball,index) in balls" :key="index">
                    <transition
                        v-on:before-enter="beforeDrop"
                        v-on:enter="Droping"
                        v-on:after-enter="afterDrop"
                    >
                        <i class="layout-add_circle ball" v-show="ball.show"></i>
                    </transition>
                </div>
            </div>
        </div>
        <transition name="list">
            <div class="list" v-show="isShow">
                <div class="header">
                    <span class="cartText"></span>
                    <span class="clear" @click="clear"></span>
                </div>
                <div class="content" ref="content">
                    <ul>
                        <li class="item" v-for="(food,index) in cartsFoods" :key="index">
                            <span class="left"> {{food.name}}</span>
                            <div class="right">
                                <span class="price_wrap">
                                    <span style="font-size:10px;color:red;text-align:center"></span>
                                    <span class="price">{{food.price}}</span>
                                </span>  
                                <carts-control class="contorl" :food="food"></carts-control>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </transition>
        <transition name="mask">
            <div class="mask" v-show="isShow" @click="maskIsShow"></div>
        </transition>
    </div> 
</template>

<script>
    import {mapState} from 'vuex'
    import BetterScroll from 'better-scroll'
    import PubSub from 'pubsub-js'
    import {transform} from '@/utile/transform.js'
    import cartsControl from 'components/carts-control/carts-control.vue'
    export default {
        name:'v-carts',
        props:{
            cartsFoods:Array
        },
        data(){
            return {
                isShow:false,
                balls:[
                    {show:false},
                    {show:false},
                    {show:false},
                    {show:false},
                    {show:false},
                ],
                dropBall:[]
            }
        },
        computed:{
            ...mapState(['seller']),
            //
            totalCount(){          
                return this.cartsFoods.reduce((adder,food)=>{
                    return adder + food.count
                },0)
            },
            //
            totalPrice(){
                return this.cartsFoods.reduce((adder,food)=>{
                    return adder + (food.count * food.price)
                },0)
            },
            //
            payText(){
                if(this.totalPrice < this.seller.minPrice && this.totalPrice !== 0){
                    return `${this.seller.minPrice - this.totalPrice}` 
                } else if(this.totalPrice >= this.seller.minPrice) {
                    return ``
                } else if(this.totalPrice === 0) {
                    if(this.seller.minPrice === undefined) return "" 
                    return `${this.seller.minPrice}`
                }  else return ''                    
            }
        },
        methods:{
             // 0  
            maskIsShow(){
                return this.isShow = false
            },
            // 0  
            listIsShow(){
                if(this.totalCount <= 0) return 
                this.isShow = !this.isShow
                this.scroll() // 
            },
            //
            clear(){
                this.$emit('clear')
            },
            //
            scroll(){
                this.$nextTick(()=>{
                    // cartsFoods    dom 
                   if(!this.contentScroll) this.contentScroll = new BetterScroll(this.$refs.content,{click:true})
                   else this.contentScroll.refresh()
                })    
            },
            showBall(name,event){  //
                //  false  true
                for(let i = 0;i<this.balls.length;i++){
                    let ball = this.balls[i]
                    if(!ball.show) {
                        ball.show = true
                        ball.target = event.target
                        this.dropBall.push(ball)
                        return
                    }
                }
            },
            beforeDrop(el){ //
                // balls  true  false 
                let ballsCount = this.balls.length
                while(0 < ballsCount) {
                    ballsCount--
                    let ball = this.balls[ballsCount]
                    if(ball.show) {
                        let viewHeight = document.documentElement.clientHeight //
                        let Y = viewHeight - ball.target.getBoundingClientRect().bottom - 10 - 12 - 7
                        let X = ball.target.getBoundingClientRect().left + 10 - 10 - 24 
                        transform(el,'translateX',X)
                        transform(el,'translateY',-Y)
                        transform(el,'scaleX',1)
                        transform(el,'scaleY',1)
                        return
                    }
                }
            },
            Droping(el){ //
                let hook = el.offsetHeight //js
                this.$nextTick(()=>{
                    transform(el,'translateX',0)
                    transform(el,'translateY',0)
                    transform(el,'scaleX',2)
                    transform(el,'scaleY',2)
                })
                this.hook = hook // hook
            },
            afterDrop(el){ //
                let ball = this.dropBall.shift()
                if(ball.show) {
                    ball.show = false
                    el.style.display = 'none' // none 
                }
        }
        },
        watch:{
            totalCount(){ //  0 
                if(this.totalCount <=0 && this.isShow) this.isShow = false
            },
        },
        mounted(){
            PubSub.subscribe('showBall',this.showBall)
        },
        components:{
            'carts-control':cartsControl
        }
    }
</script>


<style scoped lang="stylus" rel="stylesheet/stylus">
     @import "../../common/stylus/mix.styl"
    .cart
        flex 0 0 0;
        position fixed
        z-index 3
        bottom 0
        left 0
        height 46px
        width 100%
        background #141d27
        display flex
        .left
            flex 1
            height 100%
            display flex
            .icon
                position relative
                bottom 15px
                width 56px
                height 56px
                border-radius 50%
                background #141d27
                margin-left 12px
                margin-right 12px
                display flex
                justify-content center
                align-items center
                .logo
                    display flex
                    justify-content center
                    align-items center
                    width 44px
                    height 44px
                    border-radius 50%
                    background rgba(255,255,255,0.4)
                    &.active
                        background rgba(0,160,220,1)
                        i
                            color white
                    i
                        color rgba(255,255,255,0.4)
                .qipao
                    position absolute
                    right 0
                    top 5px
                    width 24px
                    height 16px
                    z-index 9
                    line-height 16px
                    border-radius 6px
                    background red
                    font-size 9px
                    font-weight 700
                    text-align center
            .totalPrice
                display flex
                justify-content center
                align-items center
                margin-right 12px
                font-size 16px
                line-height 24px
                font-weight 700
                color rgba(255,255,255,.4)
                &.active
                    color white
            .deliveryPrice
                display flex
                justify-content center
                align-items center
                font-size 14px
                line-height 24px
                font-weight 700
                color rgba(255,255,255,.4)
                padding-left 12px
                border-left 2px solid rgba(255,255,255,0.1)
        .right
            flex 0 0 105px
            height 100%
            background rgba(255,255,255,0.4)
            display flex
            justify-content center
            align-items center
            font-size 14px
            &.active
                background green
                span
                    color white
            span
                color rgba(255,255,255,0.6)
        .balls
            .ballWarp
                .ball   
                    position absolute
                    bottom 24px
                    left 30px
                    width 20px
                    height 20px
                    // background-color rgba(0,160,220,1)
                    border-radius 50%
                    transition .5s all  !important
                    &.layout-add_circle
                        text-align center
                        font-size 20px
                        color rgba(0,160,220,1) 
    .list
        max-height 255px
        position fixed
        z-index 2
        left 0
        bottom 45px
        width 100%
        background #f3f5f1
        padding-bottom 20px
        transform translate3d(0,0,0)
        .header
            border-mix(rgba(7,17,27,.1))
            height 40px
            background #f3f5f7
            display flex
            justify-content space-between
            align-items center
            .cartText
                margin-left 18px ;
                color rgba(7,17,27,1);
                font-weight 800
                font-size 14px
            .clear
                margin-right 18px
                color rgba(0,160,220,1);
                font-weight 800
                font-size 14px

        .content
            max-height 195px
            overflow hidden
            .item
                border-mix(rgba(7,17,27,.1))
                display flex
                height 48px
                align-items center
                justify-content space-between
                .left
                    padding-left 18px
                .right
                    display flex
                    align-items center
                    padding-right  22px
                    .price_wrap
                        box-sizing border-box
                        display flex
                        align-items center
                        padding-bottom 2px
                        margin-right 12px
                        height 24px
                        line-height 24px
                        .price
                            color rgba(240,20,20,1)
                            font-size 12px
                            font-weight 700
    .mask
        position fixed
        z-index 1
        left 0
        right 0
        top 0
        bottom 0
        background rgba(7,17,27,.6)
        backdrop-filter blur(5px)
</style>
